export default {
    setAvailableFields(state, values) {
        state.availableFieldsForJira = values.JiraFields;
        state.availableFieldsForTfs = values.TfsFields;
    },
    setProfileFilters(state, values) {
        const jiraProfileFilters = {
            filterMatching: values.JiraFilterMatching,
            filters: []
        };
        const tfsProfileFilters = {
            filterMatching: values.TfsFilterMatching,
            filters: []
        };
        values.Filters.forEach(filter => {
            if (filter.FilterType === 'Jira') {
                jiraProfileFilters.filters.push(filter);
            } else {
                tfsProfileFilters.filters.push(filter);
            }
        });
        state.profileFiltersDataForJira = jiraProfileFilters;
        state.profileFiltersDataForTfs = tfsProfileFilters;
    },
    updateProfileFiltersData(state, values) {
        state.profileFiltersDataForJira.filters = values.profileFiltersForJira;
        state.profileFiltersDataForTfs.filters = values.profileFiltersForTfs;
    },
    updateFilter(state, updatedFilter) {
        let allFilters;
        switch (updatedFilter.FilterType.toUpperCase()) {
            case 'JIRA':
                allFilters = state.profileFiltersDataForJira.filters;
                break;
            case 'TFS':
                allFilters = state.profileFiltersDataForTfs.filters;
                break;
            default:
                throw new Error(`Unknown filter type: ${updateData.filterType}`);
        }
        const indexOfOldFilter = allFilters.findIndex(filter => filter.FilterId === updatedFilter.FilterId);
        allFilters.splice(indexOfOldFilter, 1, updatedFilter);
    },
    updateFilterMatchingType(state, updateData) {
        switch (updateData.filterType.toUpperCase()) {
            case 'JIRA':
                state.profileFiltersDataForJira.filterMatching = updateData.filterMatchingType;
                break;
            case 'TFS':
                state.profileFiltersDataForTfs.filterMatching = updateData.filterMatchingType;
                break;
            default:
                throw new Error(`Unknown filter type: ${updateData.filterType}`);
        }
    },
    addNewFilterToProfileFilter(state, newFilter) {
        switch (newFilter.FilterType.toUpperCase()) {
            case 'JIRA':
                state.profileFiltersDataForJira.filters.push(newFilter);
                break;
            case 'TFS':
                state.profileFiltersDataForTfs.filters.push(newFilter);
                break;
            default:
                throw new Error(`Unknown filter type: ${newFilter.filterType}`);
        }
    },
    showLoader(state, loaderVisible) {
        state.loaderVisible = loaderVisible;
    },
    changeIdOfActiveEditFilterView(state, filterId) {
        state.filterIdOfActiveEditFilterView = filterId;
    },
    changeFilterTypeOfActiveCreateFilterView(state, filterType) {
        state.filterTypeOfActiveCreateFilterView = filterType;
    },
    clearEditViewState(state) {
        state.filterIdOfActiveEditFilterView = null;
        state.filterTypeOfActiveCreateFilterView = null;
    },
    setCurrentFilterInEdit(state, filter) {
        state.currentFilterInEdit = filter;
    }
};


