<template>
    <div class="filter-section" :class="'filter-section-' + dictionary.filterType">
        <div class="filter-header-container">
            <span class="filter-header-text">{{ dictionary.filterType }} filters</span>
            <button
                v-if="!filterIdOfActiveEditFilterView && !filterTypeOfActiveCreateFilterView"
                class="add-filter-button button-link filter-text"
                @click="activateCreateView({ filterType: dictionary.filterType })"
            >
                + Add {{ dictionary.filterType }} Filter
            </button>
        </div>
        <div v-if="numberOfFilters > 1" class="filter-matching-section">
            <span class="filter-text">Each {{ dictionary.filterItemName }} will be synchronized when it matches</span>
            <select class="ts4jira-select2-form-field short-field small-left-separator" v-model="filterMatchingType">
                <option value="AtLeastOneFilter">at least one filter</option>
                <option value="AllFilters">all filters</option>
            </select>
        </div>
        <ProfileFilterEdit
            v-if="filterTypeOfActiveCreateFilterView === dictionary.filterType"
            :availableFields="availableFields"
            :filterType="dictionary.filterType"
        />
        <div class="profile-filters">
            <div v-if="numberOfFilters > 0">
                <template v-for="filter in filterData.filters">
                    <ProfileFilterEdit
                        v-if="isElementInEdit(filter.FilterId)"
                        :availableFields="availableFields"
                        :filterType="dictionary.filterType"
                    />
                    <ProfileFilterRow
                        v-else
                        :key="filter.FilterId"
                        :profileFilter="filter"
                    />
                </template>
            </div>
            <div v-else class="no-filter-info filter-text">
                Currently there are no {{ dictionary.filterType }} filters added.
            </div>
        </div>
    </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex';
import ProfileFilterEdit from './ProfileFilterEdit';
import ProfileFilterRow from './ProfileFilterRow';

export default {
    components: {
        ProfileFilterEdit,
        ProfileFilterRow
    },
    props: {
        dictionary: {
            type: Object,
            required: true
        },
        filterData: {
            type: Object,
            required: true
        },
        availableFields: {
            type: Array,
            required: true
        }
    },
    data() {
        return {
            isLocalFilterEditActive: false,
            filterMatchingType: null,
            numberOfFilters: 0
        }
    },
    computed: {
        ...mapGetters('profileFilters', ['filterIdOfActiveEditFilterView', 'filterTypeOfActiveCreateFilterView'])
    },
    watch: {
        filterData: {
            deep: true,
            handler(newFilterData) {
                this.filterMatchingType = newFilterData.filterMatching;
                this.numberOfFilters = newFilterData.filters.length;
            }
        },
        filterMatchingType(newState, previousState) {
            if (previousState) {
                this.updateFilterMatchingType({filterType: this.dictionary.filterType, filterMatchingType: newState});
            }
        },
        isFilterEditActive(newState) {
            if (!newState) {
                this.isLocalFilterEditActive = false;
            }
        }
    },
    methods: {
        ...mapActions('profileFilters', ['updateFilterMatchingType', 'activateCreateView']),
        isElementInEdit(filterId) {
            return this.filterIdOfActiveEditFilterView === filterId;
        }
    }
}
</script>



