<template>
    <div class="filters-container">
        <notification class="info" icon-name="info-circle" icon-class="info-message">
            <p>By default synchronization profile has no filters and all issues and work items are synchronized.<br/>
                When there is only one filter for Jira / TFS then these issues / work items that match this filter are
                synchronized.
            </p>
        </notification>
        <ProfileFilterSection
            :dictionary="{filterType: 'Jira', filterItemName: 'issue'}"
            :filterData="profileFiltersDataForJira"
            :availableFields="availableFieldsForJira"
        />
        <ProfileFilterSection
            :dictionary="{filterType: 'TFS', filterItemName: 'work item'}"
            :filterData="profileFiltersDataForTfs"
            :availableFields="availableFieldsForTfs"
        />
        <div v-show="loaderVisible" class="spinner"/>
    </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex';
import { NAMESPACE as APP_NAMESPACE } from '../../../store/modules/app.js';
import Notification from './../../Notification';
import ProfileFilterSection from "./ProfileFilterSection";
import ProfileFiltersService from "../../../service/profileFilters.js";
import axios from 'axios';

export default {
    components: {
        ProfileFilterSection,
        Notification
    },
    props: {
        profileGuid: {
            type: String,
            required: true
        },
        loadOnInit: {
            type: String
        }
    },
    computed: {
        ...mapGetters({
            contextPath: APP_NAMESPACE.getters.CONTEXT_PATH
        }),
        ...mapGetters('profileFilters', [
            'availableFieldsForJira',
            'availableFieldsForTfs',
            'profileFiltersDataForJira',
            'profileFiltersDataForTfs',
            'loaderVisible'
        ])
    },
    mounted: function () {
        if (this.loadOnInit !== undefined && this.loadOnInit === 'true') {
            this.initComponent();
        }
    },
    methods: {
        ...mapActions('profileFilters', ['setUpService', 'loadProfileFilterData']),
        initComponent: async function () {
            this.setUpService({
                prefix: `${this.contextPath}/api/profiles/${this.profileGuid}`,
                service: new ProfileFiltersService(axios)
            });
            await this.loadProfileFilterData();
            this.loadOnInit = 'true';
        }
    }
}
</script>
<style scoped>
    @import '../../../assets/css/common.css';
</style>



