<template>
    <div class="filter-row-show filter-text">
        <span>Field</span>
        <span class="filter-field-name bold">{{ profileFilter.FieldName }}</span>
        <span class="filter-operator">{{ profileFilter.FilterOperatorName }}</span>
        <span class="filter-field-value bold">"{{ profileFilter.FieldValue }}"</span>
        <div class="filter-operations-button-container">
            <button
                class="filter-edit-button tfs4jira-rounded-button tfs4jira-icon-button"
                @click="activateEditView({profileFilter, filterType: profileFilter.FilterType})"
            >
                <img :src="`${imagePath}edit-icon.svg`"/>
            </button>
            <button
                class="filter-delete-button tfs4jira-rounded-button tfs4jira-icon-button"
                @click="removeProfileFilter(profileFilter.FilterId)"
            >
                <img :src="`${imagePath}delete-icon.svg`"/>
            </button>
        </div>
    </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex';
import { NAMESPACE as APP_NAMESPACE } from '../../../store/modules/app';

export default {
    props: {
        profileFilter: {
            type: Object,
            required: true
        }
    },
    computed: {
        ...mapGetters({
            imagePath: APP_NAMESPACE.getters.IMAGE_PATH
        })
    },
    methods: {
        ...mapActions('profileFilters', ['removeProfileFilter', "activateEditView"])
    }
};
</script>



