"use strict";

export default class ProfileFiltersService {
    constructor(http) {
        this.http = http;
    }

    getData() {
        const self = this;
        return new Promise((resolve, reject) => {
            self.http
                .get(`${self.backendUrl}/filters`)
                .then(response => {
                    resolve(response.data);
                })
                .catch(err => {
                    reject(err);
                });
        });
    }

    removeFilter(filterId, profileFiltersData) {
        function filterProfileFiltersData(filterId, profileFiltersData) {
            if (profileFiltersData.profileFiltersForJira) {
                profileFiltersData.profileFiltersForJira = profileFiltersData.profileFiltersForJira
                    .filter(profileFilter => profileFilter.FilterId !== filterId);
            }
            if (profileFiltersData.profileFiltersForTfs) {
                profileFiltersData.profileFiltersForTfs = profileFiltersData.profileFiltersForTfs
                    .filter(profileFilter => profileFilter.FilterId !== filterId);
            }
            return profileFiltersData;
        }

        const self = this;
        return new Promise((resolve, reject) => {
            self.http
                .delete(`${self.backendUrl}/filters/${filterId}`)
                .then(() => {
                    resolve(filterProfileFiltersData(filterId, profileFiltersData));
                })
                .catch(err => {
                    reject(err);
                });
        });
    }

    updateFilterMatchingType({filterType, filterMatchingType}) {
        const self = this;
        return new Promise((resolve, reject) => {
            self.http
                .put(`${self.backendUrl}/filters/update-filter-matching`, {
                    FilterMatching: filterMatchingType,
                    FilterType: filterType
                })
                .then(() => {
                    resolve()
                })
                .catch(err => {
                    reject(err);
                });
        });
    }

    updateFilter(filter) {
        const filterObjectToSend = this.createFilterObjectToSend(filter);
        const self = this;
        return new Promise((resolve, reject) => {
            self.http
                .put(`${self.backendUrl}/filters/${filter.FilterId}`, filterObjectToSend)
                .then((response) => {
                    resolve(response.data)
                })
                .catch(err => {
                    reject(err);
                });
        });
    }

    insertFilter(filter) {
        const filterObjectToSend = this.createFilterObjectToSend(filter);
        const self = this;
        return new Promise((resolve, reject) => {
            self.http
                .post(`${self.backendUrl}/filters`, filterObjectToSend)
                .then((response) => {
                    resolve(response.data)
                })
                .catch(err => {
                    reject(err);
                });
        });
    }

    createFilterObjectToSend(filter) {
        return {
            FilterType: filter.FilterType,
            FilterOperator: filter.FilterOperatorId,
            FieldId: filter.FieldId,
            FieldName: filter.FieldName,
            FieldValue: filter.FieldValue
        };
    }
}


