import { NAMESPACE as APP_NAMESPACE } from '../../app';

let service = {};
export default {
    async loadProfileFilterData({commit, dispatch}) {
        try {
            commit('showLoader', true);
            const dataFromApi = await service.getData();
            commit('setAvailableFields', dataFromApi);
            commit('setProfileFilters', dataFromApi);
            commit('showLoader', false);
        } catch (err) {
            dispatch('handleError', err);
        }
    },
    async removeProfileFilter({commit, dispatch, getters}, filterId) {
        try {
            commit('showLoader', true);
            const profileFiltersUpdated = await service.removeFilter(filterId, {
                profileFiltersForJira: getters.profileFiltersDataForJira.filters,
                profileFiltersForTfs: getters.profileFiltersDataForTfs.filters
            });
            commit('updateProfileFiltersData', profileFiltersUpdated);
            commit('showLoader', false);
        } catch (err) {
            dispatch('handleError', err);
        }
    },
    async updateFilterMatchingType({commit, dispatch}, updateData) {
        try {
            commit('showLoader', true);
            await service.updateFilterMatchingType(updateData);
            commit('updateFilterMatchingType', updateData);
            commit('showLoader', false);
        } catch (err) {
            dispatch('handleError', err);
        }
    },
    activateCreateView({commit}, {profileFilter, filterType}) {
        commit('changeFilterTypeOfActiveCreateFilterView', filterType);
        commit('setCurrentFilterInEdit', profileFilter);
    },
    activateEditView({commit}, {profileFilter}) {
        commit('changeIdOfActiveEditFilterView', profileFilter && profileFilter.FilterId);
        commit('setCurrentFilterInEdit', profileFilter);
    },
    async upsertFilter({commit, dispatch}, filter) {
        try {
            commit('showLoader', true);
            if (filter.FilterId) {
                const updatedFilter = await service.updateFilter(filter);
                commit('updateFilter', updatedFilter);
            } else {
                const newFilter = await service.insertFilter(filter);
                commit('addNewFilterToProfileFilter', newFilter);
            }
            commit('clearEditViewState');
            commit('showLoader', false);
        } catch (err) {
            dispatch('handleError', err);
        }
    },
    setUpService({}, payload) {
        if (payload.service !== undefined) {
            service = payload.service;
        }
        if (payload.prefix !== undefined) {
            service.backendUrl = payload.prefix;
        }
    },
    handleError({commit, dispatch}, error) {
        commit('showLoader', false);
        dispatch(APP_NAMESPACE.actions.HANDLE_ERROR, error, {root: true});
    }
};


