<template>
    <div class="filter-row-edit filter-text">
        <div>
            <span class="filter-text">Field</span>
            <select
                v-model="filter.FieldId"
                class="filter-fields ts4jira-select2-form-field medium-field small-left-separator"
            >
                <option
                    v-for="field in availableFields"
                    :key="field.Key"
                    :value="field.Key"
                >
                    {{ field.Value }}
                </option>
            </select>
            <select
                v-model="filter.FilterOperatorId"
                class="filter-operators ts4jira-select2-form-field short-field small-left-separator"
            >
                <option
                    v-for="operation in availableOperations"
                    :key="operation.id"
                    :value="operation.id"
                >
                    {{ operation.name }}
                </option>
            </select>
            <input
                v-model="filter.FieldValue"
                type="text"
                placeholder="Field value"
                class="filter-field-value medium-field small-left-separator"
            />
        </div>
        <div class="filter-button-container">
            <button
                class="cancel-filter-button button-link filter-text"
                @click="clearEditViewState()"
            >
                Cancel
            </button>
            <button
                class="save-filter-button tfs4jira-rounded-button filter-button filter-text"
                @click="upsertFilter(filter)"
            >
                Save filter
            </button>
        </div>
    </div>
</template>
<script>
import { mapActions, mapGetters, mapMutations } from 'vuex';

export default {
    props: {
        availableFields: {
            type: Array,
            required: true
        },
        filterType: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            availableOperations: [
                {id: 'Equals', name: 'equals'},
                {id: 'NotEquals', name: 'not equals'},
                {id: 'StartsWith', name: 'starts with'},
                {id: 'DoesntStartWith', name: 'doesn\'t start with'},
                {id: 'Contains', name: 'contains'},
                {id: 'DoesntContain', name: 'doesn\'t contain'}
            ],
            filter: {
                FieldId: null,
                FieldName: null,
                FieldValue: null,
                FilterOperatorId: null
            }
        }
    },
    computed: {
        ...mapGetters('profileFilters', ["currentFilterInEdit"])
    },
    watch: {
        currentFilterInEdit: {
            immediate: true,
            handler(newFilter) {
                if (newFilter) {
                    this.filter = Object.assign({}, newFilter);
                } else {
                    this.filter.FilterOperatorId = this.availableOperations[0].id;
                    this.filter.FieldId = this.availableFields[0].Key;
                    this.filter.FieldName = this.availableFields[0].Value;
                    this.filter.FilterType = this.filterType;
                }
            }
        },
        filter: {
            deep: true,
            handler(newFilter) {
                const fieldDefinition = this.availableFields.find(field => field.Key === newFilter.FieldId);
                newFilter.FieldName = fieldDefinition.Value;
                const operationDefinition = this.availableOperations
                    .find(op => op.id === newFilter.FilterOperatorId);
                newFilter.FilterOperatorName = operationDefinition.name;
            }
        }
    },
    methods: {
        ...mapActions('profileFilters', ['upsertFilter']),
        ...mapMutations('profileFilters', ['clearEditViewState'])
    }
};
</script>


